function ToDoCounter(){
    const { totalToDos, completedToDos } = React.useContext(ToDoContext);
    return(
        <h2 className="ToDoCounter">Has completado {completedToDos} de {totalToDos} To Do's</h2>
    );
}

export { ToDoCounter };